.note-editor.note-frame {
  border-color: #e8ebf2; }

.note-editor.note-frame {
  border-color: #e8ebf2; }

.modal .modal-body .card {
  box-shadow: none !important; }

.modal-dialog .modal-content .modal-body p a {
  margin-right: 0; }

.alert-theme button {
  top: 33px !important;
  right: 30px !important;
  color: #fd2e64; }

.alert-theme span + span + span {
  border-left: 5px solid #fd2e64;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 14px -3px rgba(0, 0, 0, 0.2); }

.alert-theme i {
  padding: 20px;
  padding-right: 0 !important;
  margin-right: 20px !important; }

.alert-theme .progress {
  margin-top: -2px !important;
  height: 2px; }
  .alert-theme .progress .progress-bar {
    background-color: #fd2e64; }

.alert-copy {
  border: 1px solid #fd2e64;
  line-height: 1;
  display: inline-block;
  width: 300px;
  color: #fff;
  background-color: #fd2e64;
  box-shadow: 3px 3px 5px 0 #9b9b9b; }
  .alert-copy .close {
    box-shadow: none;
    color: #fff;
    line-height: 1.4;
    font-weight: 100; }

.ace-editor {
  height: 400px;
  width: 100%; }

.editor-toolbar {
  width: 100% !important; }

.CodeMirror {
  top: 0 !important; }

.irs-from, .irs-to, .irs-single {
